.testcss {
  height: 100%;
  will-change: height;
}
.testcss ul li {
  text-align: left;
}
.testcss ul li .textShadow {
  color: transparent;
  text-shadow: var(--c-text-shadow-dark);
}
.light .testcss ul li .textShadow {
  text-shadow: var(--c-text-shadow-light);
}
.testcss ul li .blur {
  display: block;
  width: 100px;
  height: 100px;
  margin: auto;
  -webkit-filter: blur(8px);
  filter: blur(8px);
}
.testcss ul li .boxShadow {
  box-shadow: 0 0 0 6px rgba(255, 255, 255, 0.2), 0 0 0 12px rgba(255, 255, 255, 0.2), 0 0 0 18px rgba(255, 255, 255, 0.2), 0 0 0 24px rgba(255, 255, 255, 0.2);
  height: 200px;
  margin: 50px auto;
  width: 400px;
}
.light .testcss ul li .boxShadow {
  box-shadow: 0 0 0 6px rgba(0, 0, 0, 0.2), 0 0 0 12px rgba(0, 0, 0, 0.2), 0 0 0 18px rgba(0, 0, 0, 0.2), 0 0 0 24px rgba(0, 0, 0, 0.2);
}
.testcss .maskImage {
  width: 250px;
  height: 187.5px;
  mask-image: url('../../aseets/imgs/star.svg');
  -webkit-mask-image: url('../../aseets/imgs/star.svg');
}
.testcss.show {
  height: 1;
}
